.grecaptcha-badge {
	display: none; }

#sttv_contact {
	button {
		@include wfull;
		padding: $small;
		margin-bottom: $tiny;
		margin-top: $tiny; }

	textarea {
		min-height: 14em; } }

.contact-page {
	display: block;

	img.hero {
		@include wfull;
		height: auto; }

	.loading_overlay {
		display: none;
		z-index: 500;
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		@include wfull;
		height: 100%;
		background-color: $bodybg; } }

